@import url(https://fonts.googleapis.com/css2?family=Saira:wdth,wght@50..125,200..800&display=swap);
button,.button{-webkit-appearance:none;appearance:none;background-color:#22A0B6;border:none;border-radius:2px;box-shadow:3px 5px 2px rgba(0,0,0,0.18);color:#071E26;cursor:pointer;display:flex;font-size:22px;font-variation-settings:"wdth" 96;font-weight:720;height:42px;justify-content:center;letter-spacing:2px;line-height:20px;outline:none;padding:12px;text-align:center;text-decoration:none;transition:background-color 0.2s ease-out 0s, font-variation-settings 0.07s ease 0.03s, font-weight 0.06s ease 0.02s}button:hover,.button:hover{background-color:#EB649F;font-variation-settings:"wdth" 100;font-weight:800}button.square-btn,.button.square-btn{width:42px}button.disabled,.button.disabled{cursor:default;pointer-events:none}body,*{font-family:'Saira', sans-serif;font-weight:800}p{font-weight:200}h1,h2,h3,p{margin:0}h2{font-size:40px;letter-spacing:1px;line-height:40px;text-transform:uppercase}h3{font-size:20px;letter-spacing:10px;line-height:20px}.arrow-left{-webkit-transform:rotate(90deg);transform:rotate(90deg)}.arrow-up{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.arrow-right{-webkit-transform:rotate(-90deg);transform:rotate(-90deg)}*{box-sizing:border-box}body{background-color:#071E26;margin:0}

.App{background:#071E26;background:linear-gradient(200deg, #071E26 0%, #092632 50%, #071E26 100%);display:flex;flex-direction:column;justify-content:center;min-height:100vh}header{display:flex;justify-content:center;padding:18px}main{display:flex;justify-content:center;width:100%}

.cls-1{fill:#CB0C5A;-webkit-filter:drop-shadow(3px 5px 2px rgba(0,0,0,0.18));filter:drop-shadow(3px 5px 2px rgba(0,0,0,0.18))}.cls-2{fill:#EB649F;-webkit-filter:drop-shadow(3px 5px 2px rgba(0,0,0,0.18));filter:drop-shadow(3px 5px 2px rgba(0,0,0,0.18))}svg{height:100%;width:294px}

.grid{display:flex;flex-direction:column;justify-content:center;opacity:0;-webkit-transform:translateY(12px);transform:translateY(12px);transition:opacity 0.4s ease-in, -webkit-transform 0.3s ease-in;transition:opacity 0.4s ease-in, transform 0.3s ease-in;transition:opacity 0.4s ease-in, transform 0.3s ease-in, -webkit-transform 0.3s ease-in}.grid.shown{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}.grid.shaking{-webkit-animation:shake 140ms linear infinite;animation:shake 140ms linear infinite}.grid .wrapper{background-color:#0F528C;border-radius:2px;box-shadow:5px 8px 2px rgba(0,0,0,0.24);padding:2px}.row{display:flex}.square{align-items:center;background-color:white;border-radius:2px;box-shadow:inset 2px 3px 1px rgba(0,0,0,0.1);display:flex;font-size:30px;height:50px;justify-content:center;line-height:30px;margin:4px;transition:background-color 0.08s ease-in;width:50px}.square.goal{background-color:#CB0C5A}.square.mine{background-color:#071E26}.square.key{background-color:#22A0B6}.square.current{background-color:#EB649F}.square svg{fill:#071E26}@-webkit-keyframes shake{0%{-webkit-transform:rotate(0);transform:rotate(0)}25%{-webkit-transform:rotate(-2deg);transform:rotate(-2deg)}75%{-webkit-transform:rotate(2deg);transform:rotate(2deg)}100%{-webkit-transform:rotate(0);transform:rotate(0)}}@keyframes shake{0%{-webkit-transform:rotate(0);transform:rotate(0)}25%{-webkit-transform:rotate(-2deg);transform:rotate(-2deg)}75%{-webkit-transform:rotate(2deg);transform:rotate(2deg)}100%{-webkit-transform:rotate(0);transform:rotate(0)}}

.modal{align-items:center;background-color:rgba(15,82,140,0.933333);display:flex;height:100%;justify-content:center;left:0;position:fixed;top:0;width:100%;z-index:1}.modal.hidden{opacity:0;pointer-events:none;z-index:-1}.modal.hidden .box{-webkit-transform:translateY(20px);transform:translateY(20px)}.modal.hidden .box h2{font-variation-settings:"wdth" 96;font-weight:720}.modal.hidden .box *{-webkit-transform:translateY(4px);transform:translateY(4px);opacity:0}.modal .box{align-items:center;background-color:#071E26;border-radius:2px;box-shadow:5px 8px 2px rgba(0,0,0,0.24);color:white;display:flex;flex-direction:column;padding:30px 60px;text-align:center;transition:-webkit-transform 0.2s ease;transition:transform 0.2s ease;transition:transform 0.2s ease, -webkit-transform 0.2s ease}.modal .box .text *:nth-child(1){transition-delay:.04s}.modal .box .text *:nth-child(2){transition-delay:.08s}.modal .box h2,.modal .box p{font-variation-settings:"wdth" 100;margin-bottom:20px;transition:font-variation-settings 0.36s ease-in, font-weight 0.36s ease-in, opacity 0.3s ease, -webkit-transform 0.18s ease;transition:font-variation-settings 0.36s ease-in, font-weight 0.36s ease-in, opacity 0.3s ease, transform 0.18s ease;transition:font-variation-settings 0.36s ease-in, font-weight 0.36s ease-in, opacity 0.3s ease, transform 0.18s ease, -webkit-transform 0.18s ease}.modal .box .buttons{display:flex}.modal .box .buttons button,.modal .box .buttons .button{background-color:#CB0C5A;color:white;margin-right:12px}.modal .box .buttons button:hover,.modal .box .buttons .button:hover{background-color:#0F528C}.modal .box .buttons button:last-child,.modal .box .buttons .button:last-child{margin-right:0}.modal img{margin:16px 0 36px;width:560px}

section{display:flex;flex-direction:column;justify-content:center;margin-bottom:20px}.text-box{align-items:center;background-color:#0F528C;border-radius:2px;box-shadow:5px 8px 2px rgba(0,0,0,0.24);display:flex;height:36px;justify-content:center;margin-top:30px;text-align:center;width:100%}.text-box h3{color:#fff}.side-bar{position:fixed;right:0}.side-bar button,.side-bar .button{border-top-right-radius:0;border-bottom-right-radius:0;margin-bottom:14px;padding:10px;transition:padding 0.18s ease}.side-bar button:hover,.side-bar .button:hover{padding:8px}.side-bar button svg,.side-bar .button svg{fill:#071E26}

.menu-grid{display:grid;grid-template-columns:repeat(5, 1fr)}.menu-grid .button.square{background-color:#22A0B6}.menu-grid .button.square.disabled{background-color:#0F528C}.menu-grid .button.square:hover{background-color:#EB649F}

